export default class stateService {
    constructor(storage) {
        this.storage = storage;
    }

    save(state) {
        this.storage.setItem('state', JSON.stringify(state));
    }

    load() {
        try {
            return JSON.parse(this.storage.getItem('state'));
        } catch (e) {
            throw new Error('Invalid state');
        }
    }
}
